<Item
  bind:this={element}
  use={usePass}
  data-value={value}
  {value}
  {selected}
  {...$$restProps}><slot /></Item
>

<script>import { onMount, onDestroy, getContext, setContext } from 'svelte';
// @ts-ignore Need to use internal Svelte function
import { get_current_component } from 'svelte/internal';
import { forwardEventsBuilder } from '@smui/common/internal';
import { Item } from '@smui/list';
const forwardEvents = forwardEventsBuilder(get_current_component());
export let use = [];
$: usePass = [forwardEvents, ...use];
const className = '';
export { className as class };
export let value = '';
let element;
const selectedText = getContext('SMUI:select:selectedText');
const selectedValue = getContext('SMUI:select:value');
setContext('SMUI:list:item:role', 'option');
$: selected = value != null && value !== '' && $selectedValue === value;
onMount(setSelectedText);
onDestroy(setSelectedText);
function setSelectedText() {
    if (selected && element) {
        $selectedText = element.getPrimaryText();
    }
}
export function getElement() {
    return element.getElement();
}
</script>
